.billingBackground {
  background-image: url('./../images/yourmove.jpg');
  width: 100%;
  background-position: center;
  background-size: cover;
  padding-top: 130px;
  padding-bottom: 30px;
  min-height: calc(100vh - 360px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.termsText {
  text-align: center;
}

.termsLink {
  text-decoration: underline;
}

#billingTermsLink {
  color: unset;
}

.billingContainer {
  display: flex;
  width: calc(900px - 40px);
  padding: 20px;
  background-color: rgba(54, 54, 54, 0.9);
}

.billingForm {
  width: calc(60% - 40px);
  padding: 20px;
  background-color: var(--white);
}

.billingInfo {
  width: calc(40% - 40px);
  padding: 20px;
}

.billingTitle {
  margin: 0px;
  margin-bottom: 20px;
  color: var(--white);
}

.billingRow {
  display: flex;
  color: var(--white);
}

.billRowTitle {
  margin: 0px;
  width: 40%;
}

.billRowVal {
  margin: 0px;
  width: 60%;
}

.billRowDesc {
  margin: 0px;
}

.rowSpace {
  margin-bottom: 20px;
}

.rowWhite {
  font-size: 1.2em;
  background-color: var(--white);
  color: var(--black);
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 20px;
}

.billRowPrice {
  margin: 0px;  
}

.methodContainer {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}

.methodTitle {
  font-size: 1.2em;
  margin: 0px;
  margin-bottom: 10px;
  text-align: center;
}

.methodIcons {
  width: 100%;
  display: flex;
  justify-content: center;
}

.methodIcon {
  height: 40px;
  width: auto;
  margin: 0px 2px;
}

.stripeIcon {
  height: 22px;
  margin-bottom: -5px;
  width: auto;
}

.billingAmountDue {
  text-align: center;
  margin: 10px 0px 5px 0px;
}

#payment-form {
  align-self: center;
  box-shadow: 0px 0px 0px 0.5px rgba(50, 50, 93, 0.1),
    0px 2px 5px 0px rgba(50, 50, 93, 0.1), 0px 1px 1.5px 0px rgba(0, 0, 0, 0.07);
  border-radius: 7px;
  padding: 10px;
}
#payment-form > input {
  border-radius: 6px;
  margin-bottom: 6px;
  padding: 12px;
  border: 1px solid rgba(50, 50, 93, 0.1);
  max-height: 44px;
  font-size: 16px;
  width: 100%;
  background: white;
  box-sizing: border-box;
}
.result-message {
  line-height: 22px;
  font-size: 16px;
}
.result-message a {
  color: rgb(89, 111, 214);
  font-weight: 600;
  text-decoration: none;
}
.hidden {
  display: none;
}
#card-error {
  color: rgb(105, 115, 134);
  font-size: 16px;
  line-height: 20px;
  margin-top: 12px;
  text-align: center;
}
#card-element {
  border-radius: 4px 4px 0 0;
  padding: 12px;
  margin-top: 6px;
  border: 1px solid rgba(50, 50, 93, 0.1);
  max-height: 44px;
  width: 100%;
  background: white;
  box-sizing: border-box;
}
#payment-request-button {
  margin-bottom: 32px;
}
/* Buttons and links */
button#submit {
  background: #5469d4;
  font-family: Arial, sans-serif;
  color: #ffffff;
  border-radius: 0 0 4px 4px;
  border: 0;
  padding: 12px 16px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  display: block;
  transition: all 0.2s ease;
  box-shadow: 0px 4px 5.5px 0px rgba(0, 0, 0, 0.07);
  width: 100%;
}
button#submit:hover {
  filter: contrast(115%);
}
button#submit:disabled {
  opacity: 0.5;
  cursor: default;
}
/* spinner/processing state, errors */
.spinner,
.spinner:before,
.spinner:after {
  border-radius: 50%;
}
.spinner {
  color: #ffffff;
  font-size: 22px;
  text-indent: -99999px;
  margin: 0px auto;
  position: relative;
  width: 20px;
  height: 20px;
  box-shadow: inset 0 0 0 2px;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
}
.spinner:before,
.spinner:after {
  position: absolute;
  content: "";
}
.spinner:before {
  width: 10.4px;
  height: 20.4px;
  background: #5469d4;
  border-radius: 20.4px 0 0 20.4px;
  top: -0.2px;
  left: -0.2px;
  -webkit-transform-origin: 10.4px 10.2px;
  transform-origin: 10.4px 10.2px;
  -webkit-animation: loading 2s infinite ease 1.5s;
  animation: loading 2s infinite ease 1.5s;
}
.spinner:after {
  width: 10.4px;
  height: 10.2px;
  background: #5469d4;
  border-radius: 0 10.2px 10.2px 0;
  top: -0.1px;
  left: 10.2px;
  -webkit-transform-origin: 0px 10.2px;
  transform-origin: 0px 10.2px;
  -webkit-animation: loading 2s infinite ease;
  animation: loading 2s infinite ease;
}
@keyframes loading {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@media only screen and (max-width: 992px) {
  .billingContainer {
    flex-direction: column-reverse;
    width: calc(320px - 20px);
    padding: 10px;
  }

  .billingForm {
    width: calc(100% - 20px);
    padding: 10px;
  }
  
  .billingInfo {
    width: calc(100% - 20px);
    padding: 10px;
  }

  #payment-form {
    padding: 0px;
  }
}